<template>
  <div id="musings">
    <div id="musings-main">
      <h1 class="component-title">Quotes</h1>
      <!-- Show titles of Musings contents -->
      <div id="musings-list" v-for="i in musings" :key="i.key">
        <div class="musing-element" :style="i.css">
          <p class="musing-sentence" v-for="s in i.sentences" :key="s">{{ s }}</p>
          <p class="musing-author">{{ i.author }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Musings",
  metaInfo: {
    title: "Musings",
    meta: [
      { charset: "utf-8" },
      {
        property: "og:title",
        content: "Musings",
        template: chunk => `${chunk} :: Myeongjae Kim`,
        vmid: "og:title"
      },
      {
        property: "og:description",
        content: "which inspire me",
        template: chunk => `${chunk}`,
        vmid: "og:description"
      }
    ]
  },
  beforeDestroy: function() {
    var nav = document.querySelector("nav");
    nav.setAttribute(
      "class",
      nav.getAttribute("class").replace(/ musing-font/gi, "")
    );
  },
  data() {
    return {
      musings: [
        {
          key: 0,
          sentences: ["  ,", "   ."],
          author: "- , <>, , 2005",
          css: ""
        },
        {
          key: 1,
          sentences: [
            "The one thing you cant take away from me is the way I choose to respond to what you do to me. The last of ones freedoms is to choose ones attitude in any given circumstance."
          ],
          author: "- Viktor E. Frankl, <Man's Search for Meaning>",
          css: "font-family: 'Bad Script'; font-style:normal;"
        },
        {
          key: 2,
          sentences: [
            " , , , , , , ",
            " 7   7   ."
          ],
          author: "-   , < , 2015 11 27>",
          css: ""
        },
        {
          key: 3,
          sentences: [
            "It seems that perfection is attained not when there is nothing more to add, but when there is nothing more to remove."
          ],
          author:
            "- Antoine de Saint Exupry, <Wind, Sand and Stars>, 1939, Chapter 3",
          css: "font-family: 'Bad Script'; font-style:normal;"
        },
        {
          key: 3,
          sentences: [
            "  '   '                 .              .    ' '  ."
          ],
          author: "- , < >, 2018,   , p160",
          css: "line-height: 2.2em;"
        }
      ]
    };
  },
  mounted: function() {
    // Add font to nav
    var nav = document.querySelector("nav");
    var attr = nav.getAttribute("class");
    if (attr == null) attr = "";
    nav.setAttribute("class", attr + " musing-font");

    [].forEach.call(document.querySelectorAll(".text"), function(el) {
      var origin = el.innerHTML;
      var new_html = "";
      for (var i = 0; i < origin.length; i++)
        new_html += "<span>" + origin[i] + "</span>";
      el.innerHTML = new_html;
    });
  }
};
</script>

<style>
.musing-font {
  font-family: "Bad Script", "Source Sans Pro", "Spoqa Han Sans", Helvetica,
    Arial, sans-serif;
}
</style>

